import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { CustomerReviewListComponent } from './customer-review-list.component';
import { LoderModule } from 'src/app/common/loder/loder.module';
import { PaginationModule } from 'src/app/common/pagination/pagination.module';
// import { RatingModule } from 'ng-starrating';
import { CustomerReviewService } from 'src/app/service/customer-review.service';

const routes: Routes = [
  {
    path: '',
    component: CustomerReviewListComponent,
    // data: { title: 'company_list' }
  },
  {
    path: 'add',
    loadChildren: () => import('../customer-review-add/customer-review-add.module').then(m => m.CustomerReviewAddModule),
    // data: { title: 'add_company' }
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('../customer-review-add/customer-review-add.module').then(m => m.CustomerReviewAddModule),
    // data: { title: 'edit_company' }
  },
];

@NgModule({
  declarations: [
    CustomerReviewListComponent
  ],
  imports: [
    CommonModule,
    LoderModule,
    PaginationModule,
    // RatingModule ,
    RouterModule.forChild(routes)
  ],
  providers: [
    CustomerReviewService
  ]
})

export class CustomerReviewListModule { }
